@mixin clearfix {
    &:after {
        content: "";
        display: table;
        clear: both;
    }
}

$color-1: #004990; /* dark blue */ 
$color-2: #ffe512; /* yellow */ 
$color-3: #d7e2ed; /* light blue */
$color-4: #7099c1; /* blue */
$color-r: #c51437; /* red */ 

$font-1: 'Futura New Book', sans-serif;
$font-2: 'Futura New', sans-serif;
$font-3: 'Proxima Nova Condensed';