/**/
*{
	padding: 0px;
	margin: 0px;
	box-sizing: border-box;
}
img{
	border:0;
	max-width:100%;
	height: auto;
}
a{
	color: #fff;
	text-decoration: none;
	transition: color 0.4s, background 0.4s, border 0.4s;
}
a:hover{
	color: #fff;
	text-decoration: underline;
}
a:focus{
 	text-decoration: none;
}
*:focus,*:active, * {
	outline: none !important;
}
*::-moz-selection {
  background: rgba(255, 229, 18, 0.3); }

*::selection {
  background: rgba(255, 229, 18, 0.3); }

body{
	font: 15px $font-1;
	line-height: 1.5;
	color: #fff;
	background: $color-1; 
	padding: 0;
}

#wrapper{
	overflow: hidden;
	position: relative;
}

.text-center{
	text-align: center;
}

/**/
h1, h2, h3, h4, h5, .h1, .h2, .h3, .h4, .h5{
	margin-top: 0px;
  margin-bottom: 0.8em;
}

/* table */
table{
  border-collapse: collapse;
  width: 100%;
  margin: 0px 0 15px;
}
table th{
  padding: 5px 12px;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 1.3em;
  border-bottom: 1px solid $color-2;
}
table td{
  padding: 5px 12px;
  font-size: 17px;
  line-height: 1.2em;
  color: $color-1;
  border-bottom: 1px solid $color-2;
}

/* forms */
input[type='text'], input[type='tel'], input[type='search'], input[type='email'], input[type='password'], input[type='number'], input[type='date'], textarea, select{
  background: #fff;
  border: none;
  margin: 0;
  width: 100%;
  height: 36px;
  padding: 0 15px;
  font-family: $font-1;
  font-size: 16px;
  border-radius: 18px;
  display: block;
  color: $color-1;
  text-align: center;
}
textarea{
  padding: 10px 15px;
  height: 150px;
  resize: none;
}

::-webkit-input-placeholder {color:#8aa9cd;transition-duration:0.2s;}
::-moz-placeholder          {color:#8aa9cd;transition-duration:0.2s;}
:-moz-placeholder           {color:#8aa9cd;transition-duration:0.2s;}
:-ms-input-placeholder      {color:#8aa9cd;transition-duration:0.2s;}

:focus::-webkit-input-placeholder {opacity: 0.8;}
:focus::-moz-placeholder          {opacity: 0.8;}
:focus:-moz-placeholder           {opacity: 0.8;}
:focus:-ms-input-placeholder      {opacity: 0.8;}

.form-group{
  margin-bottom: 9px;
  position: relative;
  font-size: 16px;
  &.with-label{
    display: flex;
    align-items: center;
    background: $color-4;
    border-radius: 18px;
    label{
      margin: 0;
      padding: 0 15px;
      min-width: 34%;
    }
    &.size-2{
      label{
        min-width: 59%;
      }
    }
  }
  &.form-group-bg-2{
    background: #3276c2;
  }
  &.radio, &.checkbox{
    padding: 0 42px;
    margin-top: 40px;
    margin-bottom: 5px;
    &+&{
      margin-top: 0;
      margin-bottom: 28px;
    }
    &.m-sm{
      margin-top: 18px;
      margin-bottom: 0;
    }
  }
  &.captcha{
    max-width: 194px;
    height: 45px;
    padding: 5px;
    margin: 0 auto 15px;
    border-radius: 23px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

/* checkbox/radio */
input[type='checkbox'], input[type='radio'] {
  vertical-align: top;
  width: 20px;
  height: 20px;
  margin: 0 3px 0 0;
}
input[type='checkbox'] + label, input[type='radio'] + label {
  cursor: pointer;
  padding-left: 38px;
  padding-top: 0px;
  position: relative;
  margin-bottom: 0px;
  display: block;
  line-height: 1.3em;
  font-size: 14px;
  &:before{
    content: '';
    position: absolute;
    left: 0px;
    top: 0;
    margin-top: 0;
    width: 18px;
    height: 18px;
    background: #fff;
    border-radius: 4px;
    border-radius: 50%;
  }
  &:after{
    content: '';
    position: absolute;
    left: 5px;
    top: 0;
    margin-top: 5px;
    width: 8px;
    height: 8px;
    opacity: 0;
    background: $color-r;
    border-radius: 2px;
    border-radius: 50%;
  }
  a{
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }
}
input[type='radio'] + label {
  &:before{
    border-radius: 50%;
  }
  &:after{
    border-radius: 50%;
  }
}
input[type='checkbox']:not(checked), input[type='radio']:not(checked) {
  position: absolute;
  opacity: 0;
  left: 0;
  top: 0;
  z-index: -1;
}
input[type='checkbox']:checked + label, input[type='radio']:checked + label {
  &:after{
    opacity: 1;
  }
}

/* button */
.btn{
  display: inline-block;
  min-width: 400px;
  padding: 0 15px;
  margin: 45px 0 20px;
  color: $color-1;
  background-color: $color-2;
  border: none;
  font-size: 25px;
  line-height: 52px;
  font-family: $font-3;
  font-weight: 900;
  font-style: italic;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  border-radius: 26px;
  position: relative;
  z-index: 0;
  transition-duration: 0.4s;
  overflow: hidden;
  vertical-align: middle;
  &:after{
    content: 'â€º';
    font-size: 50px;
    margin-left: 21px;
    display: inline-block;
    line-height: 0.8em;
    vertical-align: text-bottom;
    margin-bottom: -2px;
  }
  &:hover{
    text-decoration: none;
  }
  &.btn-style-2{
    background-color: $color-r;
    color: #fff;
  }

  &.btn-m-sm{
    margin-top: 20px;
    margin-bottom: 20px;
  }

  &.btn-sm{
    font-size: 17px;
    font-family: $font-1;
    font-weight: normal;
    text-transform: none;
    min-width: 0;
    padding: 0 15px 2px;
    line-height: 26px;
    margin: 0;
    &:after{
      font-size: 28px;
      font-family: $font-3;
      font-weight: 900;
      margin-left: 10px;
    }
  }
}

.scroll-box{
  height: 290px;
  overflow: hidden;
  padding: 10px 0;
}
.mCSB_inside > .mCSB_container{
  margin-right: 65px !important;
}
.mCSB_scrollTools{
  width: 18px;
}
.mCSB_outside+.mCS-minimal-dark.mCSB_scrollTools_vertical, .mCSB_outside+.mCS-minimal.mCSB_scrollTools_vertical {
    right: 0px;
    margin: 0px 0;
    opacity: 1;
}
.mCSB_scrollTools .mCSB_dragger {
  background: $color-2;
  border-radius: 8px; }

.mCSB_scrollTools .mCSB_draggerRail {
    width: 18px;
    background: #6496c9;
    border-radius: 8px;
}

.mCSB_outside + .mCS-minimal.mCSB_scrollTools_vertical, .mCSB_outside + .mCS-minimal-dark.mCSB_scrollTools_vertical {
  background: #6496c9;
  border-radius: 8px; }

.mCS-minimal.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar, .mCS-minimal.mCSB_scrollTools .mCSB_draggerRail, .mCS-minimal-dark.mCSB_scrollTools .mCSB_draggerRail {
  background: none !important; }

@media (max-width: 1300px){
  input[type='text'], input[type='tel'], input[type='search'], input[type='email'], input[type='password'], input[type='number'], input[type='date'], textarea, select{
    height: 28px;
    padding: 0 15px;
    font-size: 12px;
    border-radius: 14px;
  }
  .form-group{
    margin-bottom: 7px;
    font-size: 12px;
    &.with-label{
      border-radius: 18px;
      label{
        padding: 0 10px;
      }
    }
    &.radio, &.checkbox{
      padding: 0 15px 0 25px;
      margin-top: 30px;
      margin-bottom: 3px;
      &+&{
        margin-top: 0;
        margin-bottom: 20px;
      }
      &.m-sm{
        margin-top: 12px;
      }
    }
    &.captcha{
      max-width: 150px;
      height: 35px;
      padding: 5px;
      margin: 0 auto 12px;
      border-radius: 18px;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
  input[type='checkbox'] + label, input[type='radio'] + label {
    padding-left: 27px;
    font-size: 12px;
  }
  .btn{
    min-width: 300px;
    margin: 32px 0 15px;
    font-size: 19px;
    line-height: 40px;
    border-radius: 20px;
    &:after{
      font-size: 38px;
      margin-left: 15px;
      line-height: 0.8em;
      margin-bottom: 0px;
    }
    &.btn-m-sm{
      margin-top: 15px;
      margin-bottom: 15px;
    }
    &.btn-sm{
      font-size: 13px;
      padding: 0 10px 1px;
      line-height: 20px;
      white-space: nowrap;
      &:after{
        font-size: 20px;
        margin-left: 8px;
      }
    }
  }
  table{
    margin: 0px 0 10px;
  }
  table th{
    padding: 3px 10px;
    font-size: 14px;
  }
  table td{
    padding: 3px 10px;
    font-size: 13px;
  }
  .scroll-box{
    height: 200px;
    padding: 0px 0;
  }
  .mCSB_scrollTools{
    width: 14px;
  }
  .mCSB_scrollTools .mCSB_draggerRail {
      width: 14px;
  }
}

@media only screen and (max-width: 575px) {
  .btn{
    min-width: 244px;
    margin: 20px 0 10px;
    font-size: 15px;
    line-height: 32px;
    border-radius: 16px;
    &:after{
      font-size: 31px;
      margin-left: 15px;
      line-height: 0.8em;
      margin-bottom: 0px;
    }
    &.btn-m-sm{
      margin-top: 15px;
      margin-bottom: 15px;
    }
    &.btn-sm{
      min-width: 244px;
      margin: 0 0 3px;
      font-size: 15px;
      line-height: 32px;
      border-radius: 16px;
      font-family: $font-3;
      text-transform: uppercase;
      &:after{
        font-size: 31px;
        margin-left: 15px;
        line-height: 0.8em;
        margin-bottom: 0px;
      }
    }
  }
  .form-group.with-label label{
    padding: 0 7px;
  }
  .form-group.radio, .form-group.checkbox{
    padding: 0 15px;
  }
}