@import "variables";

/* main-screen */
.main-screen{
  background: $color-1;
  background-size: cover;
  position: relative;
  overflow: hidden;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  &:before, &:after{
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    background-repeat: no-repeat;
    background-size: contain;
  }
  &:before{
    left: 0;
    width: 22.5%;
    background-image: url(../images/bg-left.png);
    background-position: left center;
  }
  &:after{
    right: 0;
    width: 17.7%;
    background-image: url(../images/bg-right.png);
    background-position: right center;
  }
}

.main-body{
  position: relative;
  z-index: 5;
  width: 100%;
}
.path-wrap{
  max-width: 84%;
  margin: 0 auto;
  padding-left: 1%;
}
.path{
  position: relative;
  .path-d, .path-t, .path-m{
    width: 100%;
  }
  .path-t, .path-m{
    display: none;
  }
  .image-box{
    position: absolute;
    img, svg{
      width: 100%;
    }
  }
  .text-box{
    position: absolute;
    font-family: $font-2;
    font-weight: bold;
    font-style: italic;
    color: #fff;
    text-transform: uppercase;
    font-size: 22px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    >span{
      display: block;
      position: relative;
      line-height: 0.9em;
    }
    .yellow{
      color: $color-2;
      font-size: 26px;
      line-height: 1em;
      letter-spacing: 0.3px;
      .codes{
        margin: -10px 0 0 -18px;
      }
    }
    .codes{
      position: absolute;
      left: 100%;
      top: 100%;     
      margin: -6px 0 0 -18px;
      font-size: 14px;
      white-space: nowrap;
      color: #fff;
      span{
        font-size: 27px;
      }
    }
    .hint{
      margin: 2px 0 0 3px;
    }
  }

  .image-box-1{
    left: 12.8%;
    top: 14%;
    width: 5.3%;
    transform: rotate(17deg);
  }
  .text-box-1{
    left: 10.54%;
    top: 29%;
    >span:nth-child(2){
      margin-left: 38px;
    }
  }

  .image-box-2{
    left: 30.8%;
    top: 16%;
    width: 11.3%
  }
  .text-box-2{
    left: 41.8%;
    bottom: 72.8%;
    >span:nth-child(1){
      margin-left: 38px;
    }
    >span:nth-child(2){
      margin-left: 19px;
    }
  }

  .image-box-3{
    left: 36.3%;
    top: 36.5%;
    width: 17%;
  }
  .text-box-3{
    right: 64.1%;
    top: 55.5%;
    >span:nth-child(1){
      margin-left: 107px;
    }
    >span:nth-child(2){
      margin-left: 45px;
    }
  }

  .image-box-4{
    right: 1.5%;
    bottom: 7%;
    width: 32.4%;
    .light{
      position: absolute;
      width: 107.6%;
      max-width: none;
      right: 19.2%;
      top: 20.2%;
      opacity: 0;
    }
  }
  .text-box-4{
    left: 65.4%;
    bottom: 61.1%;
    .yellow{
      font-size: 30px;
      letter-spacing: 0.5px;
    }
    >span:nth-child(1){
      margin-left: 20px;
    }
  }
}

/* */
.key-form-wrap{
  position: relative;
  max-width: 490px;
  margin: -64px auto 45px;
  font-family: $font-3;
  font-weight: 900;
  font-style: italic;
  border-radius: 20px;
}
.key-form{
  position: relative;
  .key-input{
    background: #fff;
    height: 64px;
    border-radius: 20px;
    font-size: 40px;
    text-transform: uppercase;
    color: $color-1;
    padding: 0 100px 0 15px;
    font-family: $font-3;
    font-weight: 900;
    font-style: italic;
  }
  .key-btn{
    position: absolute;
    top: 0px;
    right: 0px;
    height: 64px;
    width: 82px;
    border-radius: 20px;
    border: none;
    background: $color-r;
    color: #fff;
    font-size: 71px;
    line-height: 0em;
    padding-bottom: 8px;
    &:after{
      content: 'â€º';
    }
  }
}
.over-key{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 64px;
  background: $color-r;
  padding: 0 15px;
  border-radius: 20px;
  text-align: center;
  transition: transform 0.5s;
  cursor: pointer;
  color: #fff;
  font-size: 40px;
  line-height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  &:after{
    content: 'â€º';
    font-size: 71px;
    margin-left: 15px;
    display: inline-block;
    margin-top: -13px;
  }
}
.key-form-wrap.active{
  overflow:hidden;
  .over-key{
    transform: translateX(100%);
  }
}

/* main-title */
.main-title{
  text-align: center;
  color: #fff;
  font-family: $font-2;
  font-weight: 800;
  font-style: italic;
  text-transform: uppercase;
  font-size: 49px;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-direction: column;
  padding-top: 41px;
  .text-t{
    display: block;
    font-size: 70px;
    line-height: 1.2em;
    margin-right: 524px;
  }
  .text-b{
    display: block;
    position: relative;
    line-height: 0.75em;
    position: relative;
    .before, .after{
      white-space: nowrap;
      opacity: 0.05;
      position: absolute;
      top: 0;
      bottom: 0;
    }
    .before{
      right: 100%;
      margin-right: 5px;
    }
    .after{
      left: 100%;
      margin-left: 5px;
    }
  } 
}

/* main-products */
.main-products{
  position: absolute;
  left: 0;
  bottom: 63px;
  width: 23.9%;
  z-index: 1;
}

/* main-footer */
.main-footer{
  padding: 13px 15px;
  background: $color-3;
  position: relative;
  z-index: 5;
  width: 100%;
  font-family: $font-2;
  font-weight: bold;
  font-style: italic;
  display: flex;
  align-items: center;
  justify-content: center;
  .items{
    display: flex;
    flex-wrap: wrap;
    margin-right: 55px;
    .item{
      font-size: 31px;
      color: $color-1;
      text-transform: uppercase;
      display: flex;
      align-items: center;
      padding: 0 24px;
      .number{
        font-size: 69px;
        color: $color-4;
        margin-right: 8px;
        line-height: 1em;
      }
      .text{
        letter-spacing: 0.5px;
        i.hint{
          margin-left: -8px;
        }
      }
      .images{
        display: flex;
        align-items: center;
        img{
          margin: 0 3px;
          max-width: 56px;
          max-height: 42px;
          display: block;
          margin-right: 10px;
          margin-top: -7px;
        }
      }
    }
  }
}
.sociale{
  display: flex;
  align-items: center;
  margin-right: 155px;
  a{
    width: 57px;
    height: 57px;
    border-radius: 50%;
    border: 5px solid $color-4;
    background: #fff;
    margin: 0 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      max-width: 30px;
      max-height: 32px;
    }
    &.telegram{
      img{
        margin-left: -3px;
      }
    }
  }
  &.sociale-mobile{
    display: none;
  }
}

i.hint{
  display: inline-block;
  width: 16px;
  height: 16px;
  background: #fff;
  border: 2px solid $color-4;
  color: $color-r;
  font-size: 14px;
  line-height: 12px;
  text-align: center;
  border-radius: 50%;
  text-transform: none;
  font-weight: 800;
  cursor: pointer;
  vertical-align: text-top;
  margin: -7px 0 0 0;
}
.tooltip {
  position: absolute;
  z-index: 1070;
  display: block;
  margin: 16px 0 0;
  opacity: 0;
  max-width: 370px;
  background: rgba(99,144,187,0.67);
  border-radius: 20px;
  padding: 30px 45px 15px;
  text-align: center;
  color: $color-1;
  line-height: 1.4em;
  &:before{
    content: '';
    position: absolute;
    z-index: 1;
    left: 7px;
    right: 7px;
    top: 7px;
    bottom: 7px;
    border-radius: 20px;
    background: rgba(215,226,237,0.91);
    transform: rotate(-0.9deg);
  }
  .tooltip-inner{
    position: relative;
    z-index: 2;
  }
  .arrow{
    &:before{
      content: '';
      position: absolute;
      left: 50%;
      bottom: 100%;
      margin-left: 15px;
      transform: skewX(30deg);
      border-top: 10px solid transparent;
      border-right: 30px solid transparent;
      border-bottom: 10px solid #6390bb;
      border-left: 30px solid #6390bb;
      opacity: 0.67;
    }
    &:after{
      content: '';
      position: absolute;
      left: 50%;
      bottom: 100%;
      margin-left: 25px;
      margin-bottom: -7px;
      transform: skewX(30deg) rotate(-0.9deg);
      border-top: 15px solid transparent;
      border-right: 33px solid transparent;
      border-bottom: 15px solid #d7e2ed;
      border-left: 33px solid #d7e2ed;
      opacity: 0.91;
    }
  }
  &.show {
    opacity: 1;
  }

  &.bs-tooltip-top{
    margin: 0 0 33px;
    max-width: 395px;
    &:before{
      transform: rotate(0.9deg);
    }
    .arrow{
      &:before{
        bottom: auto;
        top: 100%;
        margin-left: 20px;
        transform: skewX(-30deg);
        border-bottom: 15px solid transparent;
        border-top: 15px solid #6390bb;
      }
      &:after{
        bottom: auto;
        top: 100%;
        margin-left: 30px;
        margin-top: -7px;
        margin-bottom: 0;
        transform: skewX(-30deg) rotate(0.9deg);
        border-bottom: 20px solid transparent;
        border-top: 20px solid #d7e2ed;
      }
    }
  }
}

