@media (max-width: 460px){
  .tooltip {
    max-width: 200px;
    background: rgba(256,256,256,0.50);
    border-radius: 15px;
    padding: 30px 25px 15px;
    line-height: 1.4em;
    font-size: 13px;
    &:before{
      left: 7px;
      right: 7px;
      top: 7px;
      bottom: 7px;
      border-radius: 15px;
      background: rgba(256,256,256,0.88);
      transform: rotate(-0.9deg);
    }
    p{
      margin-bottom: 1em;
    }
    .arrow{
      &:before{
        display: none;
      }
      &:after{
        content: '';
        position: absolute;
        left: 50%;
        bottom: 100%;
        margin-left: 25px;
        margin-bottom: -7px;
        transform: skewX(30deg) rotate(-0.9deg);
        border-top: 15px solid transparent;
        border-right: 20px solid transparent;
        border-bottom: 15px solid #fff;
        border-left: 20px solid #fff;
        opacity: 0.88;
      }
    }
    &.bs-tooltip-top{
      max-width: 200px;
      br{
        display: none;
      }
      .arrow{
        &:after{
          margin-left: 18px;
          border-top: 20px solid #fff;
        }
      }
    }
  }
}