/* animations */
.loaded .headlight{
	animation-name: headlights;
	animation-iteration-count: infinite;
	animation-timing-function: cubic-bezier(1.0,0,0,1.0);
	animation-duration: 4s;
}
.loaded .light{
	animation-name: light;
	animation-iteration-count: infinite;
	animation-timing-function: cubic-bezier(1.0,0,0,1.0);
	animation-duration: 4s;
}
@keyframes headlights{
  0% { fill: #d60021; }
  4% { fill: #148DD8; }
  12% { fill: #d60021; }
  17% { fill: #148DD8; }
  20% { fill: #d60021; }
}
@keyframes light{
  0% { opacity: 0; }
  4% { opacity: 1; }
  12% { opacity: 0; }
  17% { opacity: 1; }
  20% { opacity: 0; }
}

.loaded .snowboarder{
  animation-name: none;
  animation-timing-function: cubic-bezier(1.0,0.5,0.5,1.0);
  animation-duration: 1.5s;
  transform-origin: 40% 50%;
}
@keyframes snowboarder{
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}