@import "variables";

/* modal */
.modal-open{
  overflow: hidden;
  .modal{
    overflow-x: hidden;
    overflow-y: auto;
  }
}
.modal{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
}
.modal-dialog {
  position: relative;
  pointer-events: none;
  max-width: 515px;
  margin: 0.5rem auto;
  &.modal-dialog-big{
    max-width: 920px;
  }
}

.modal-dialog-centered {
  display: flex;
  align-items: center;
  min-height: calc(100% - (.5rem * 2));
  margin: 0.5rem auto;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: $color-1;
  transition-duration: 0.2s;
}
.modal-backdrop.fade {
  opacity: 0;
}
.modal-backdrop.show {
  opacity: 0.70;
}

.modal button.close {
  position: absolute;
  top: 17px;
  right: 7px;
  width: 21px;
  height: 21px;
  margin: 0;
  opacity: 1;
  background: #fff;
  border: none;
  border-radius: 50%;
  z-index: 10;
  transition-duration: 0.2s;
  font-family: $font-3;
  font-weight: 900;
  font-style: italic;
  font-size: 28px;
  text-align: center;
  line-height: 21px;
  color: $color-r;
  padding-top: 1px;
  padding-right: 1px;
  &:hover{
    opacity: 0.9;   
  }
}

.modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-clip: padding-box;
  outline: 0;
  &:before, &:after{
    content: '';
    position: absolute;
    border-radius: 20px;
    transform-origin: center center;
  }
  &:before{
    top: -8px;
    bottom: -8px;
    left: -9px;
    right: -9px;
    z-index: 1;
    background: #fff;
    transform: skew(1deg, -0.3deg);
  }
  &:after{
    left: -1px;
    right: -1px;
    top: 5px;
    bottom: 5px;
    z-index: 3;
    background: $color-1;
    transform: skewY(0.8deg);
  }
  >div{
    position: relative;
    z-index: 5;
  }

  h2, .h2{
    text-align: center;
    text-transform: uppercase;
    font-size: 25px;
    font-family: $font-2;
    font-weight: 800;
    font-style: italic;
    margin: 0 0 0.8em;
    line-height: 1.3em;
    color: #fff;
    &.warning{
      color: $color-2;
    }
    &.danger{
      margin: 0;
      color: $color-r;
      text-shadow: 1px 1px 0px #fff, -1px -1px 0px #fff, -1px 1px 0px #fff, 1px -1px 0px #fff;
    }
  }
  h3, .h3{
    text-align: center;
    text-transform: none;
    font-size: 25px;
    font-family: $font-2;
    font-weight: 900;
    font-style: italic;
    margin: 0 0 1em;
    line-height: 1.3em;
    &.warning{
      color: $color-2;
    }
    &.danger{
      color: $color-r;
      text-shadow: 1px 1px 0px #fff, -1px -1px 0px #fff, -1px 1px 0px #fff, 1px -1px 0px #fff;
    }
  }
  h4, .h4{
    text-align: center;
    text-transform: none;
    font-size: 20px;
    margin: 0;
    line-height: 1.3em;
    font-family: $font-3;
    font-weight: 900;
    font-style: italic;
    letter-spacing: 1px;
    &.warning{
      color: $color-2;
    }
    &.danger{
      color: $color-r;
      text-shadow: 1px 1px 0px #fff, -1px -1px 0px #fff, -1px 1px 0px #fff, 1px -1px 0px #fff;
    }
  }
  h5, .h5{
    text-align: center;
    text-transform: none;
    font-size: 17px;
    margin: 0;
    line-height: 1.25em;
    color: $color-4;
    font-family: $font-2;
    font-weight: 800;
    font-style: italic;
    &.warning{
      color: $color-2;
    }
    &.danger{
      color: $color-r;
      text-shadow: 1px 1px 0px #fff, -1px -1px 0px #fff, -1px 1px 0px #fff, 1px -1px 0px #fff;
    }
  }
  .danger{
    color: $color-r;
  }
  .m-0{
    margin: 0;
  }
}

.modal-head{
  padding: 56px 15px 31px;
  text-align: center;
  p{
    font-size: 17px;
    margin: 0;
  }
  &.with-desc{
    padding-bottom: 25px;
    .h2{
      margin-bottom: 0.2em;
    }
  }
  &.p-sm{
    padding-bottom: 0;
    padding-top: 45px;
  }
}
.modal-body{
  padding: 0px 15px 50px;
  &.pb-sm{
    padding-bottom: 30px;
  }
  a{
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }
  .message{
    text-align: center;
    margin: 0 auto 20px;
    line-height: 1.6em;
    font-size: 25px;
    max-width: 445px;
    &.bold{
      font-weight: 900;
      line-height: 1.2em;
      letter-spacing: 0.5px;
      a{
        text-transform: uppercase;
      }
    }
    .h2{
      font-weight: 700;
      text-transform: uppercase;
    }
    .h3{
      font-weight: 700;
      text-transform: none;
     }
  }
  .form{
    max-width: 424px;
    margin: 0 auto;
  }
}

.box-center{
  text-align: center;
  padding-top: 20px;
  margin: 0 auto;
  max-width: 420px;
  p{
    font-size: 17px;
  }
  .btn{
    margin: 10px 0 0;
  }
}

.modal-footer{
  padding: 0 15px 20px;
  color: #89a8cd;
  font-size: 17px;
  text-align: center;
  line-height: 1.4em;
  p{
    margin: 0;
  }
  a{
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }
}

.recovery-link{
  text-align: center;
  font-size: 16px;
  margin-bottom: 22px;
}

.footnote{
  font-size: 14px;
  margin: 0;
  text-align: center;
  line-height: 1.3em;
}

#registrationAddress{
  .modal-head{
    padding-top: 40px;
  }
  .modal-body{
    padding-bottom: 20px;
  }
}

.account-top{
  display: flex;
  font-size: 25px;
  font-family: $font-2;
  font-weight: 900;
  font-style: italic;
  margin-bottom: 20px;
  line-height: 1.2em;
  .my-info{
    width: 60%;
    padding: 0 35px;
  }
  .my-keys{
    width: 40%;
    padding: 0 35px;
    text-align: center;
  }
  a{
    text-decoration: none;
  }
  p{
    margin: 0;
  }
  .name, .title{
    color: $color-2;
    margin-bottom: 12px;
  }
}
.my-statistic-wrap{
  background: #6496c9;
  border-radius: 15px;
  margin-bottom: 25px;
  .h2{
    margin: 0;
    padding: 10px 0;
  }
}
.my-statistic{
  background: #fff;
  border-radius: 15px;
  padding: 7px 7px 7px 30px;
  td{
    text-align: center;
    line-height: 1.6em;
    &:first-child{
      text-align: left;
    }
  }
  .mCSB_inside > .mCSB_container{
    margin-right: 65px;
  }
}

.account-btn-group{
  display: flex;
  justify-content: space-between;
  .btn{
    margin: 0;
  }
  .btn-style-2{
    min-width: 227px;
  }
}

/**/
.winners{
  background: #fff;
  border-radius: 15px;
  padding: 7px 7px 7px 45px;
  margin-bottom: 20px;
  margin-top: 30px;
  td{
    font-family: $font-2;
    font-weight: 800;
    font-style: italic;
    font-size: 17px;
    text-align: center;
    text-transform: uppercase;
  }
}

/**/
.official-rules{
  background: #fff;
  border-radius: 15px;
  padding: 7px 7px 7px 7px;
  color: $color-1;
  font-size: 14px;
  line-height: 1.25em;
  text-align: center;
  margin-top: 15px;
  .scroll-box{
    height: 520px;
    padding: 0;
  }
  .mCSB_inside > .mCSB_container {
    margin-right: 0 !important;
    padding: 0 40px 0 20px;
  }
  h3{
    font-weight: 700;
    font-family: $font-2;
    margin-bottom: 0.7em;
  }
  p{
    margin-bottom: 1em;
  }
}

@media (max-width: 1300px){
  .modal-dialog {
    max-width: 360px;
    &.modal-dialog-big{
      max-width: 690px;
    }
  }
  .modal button.close {
    top: 17px;
    right: 7px;
    width: 21px;
    height: 21px;
    font-size: 28px;
    line-height: 21px;
  }

  .modal-content {
    &:before, &:after{
      border-radius: 15px;
    }
    &:before{
      top: -5px;
      bottom: -5px;
      left: -6px;
      right: -6px;
    }
    &:after{
      left: 0px;
      right: 0px;
      top: 3px;
      bottom: 3px;
    }
    h2, .h2{
      font-size: 19px;
    }
    h3, .h3{
      font-size: 19px;
    }
    h4, .h4{
      font-size: 16px;
    }
    h5, .h5{
