    }
  }

  .modal-head{
    padding: 35px 15px 20px;
  }
  .modal-body{
    padding: 0px 15px 35px;
    .message{
      margin-bottom: 15px;
      line-height: 1.9em;
      font-size: 15px;
    }
    &.pb-sm{
      padding-bottom: 26px;
    }
    .message{
      max-width: 320px;
    }
    .form{
      max-width: 320px;
    }
  }

  .modal-footer {
    padding: 0 10px 20px;
    font-size: 13px;
  }

  .account-top{
    font-size: 19px;
    margin-bottom: 15px;
    .my-info{
      width: 50%;
      padding: 0 30px;
    }
    .my-keys{
      width: 50%;
      padding: 0 30px;
    }
    .name, .title{
      margin-bottom: 10px;
    }
  }
  .my-statistic-wrap{
    margin-bottom: 20px;
    .h2{
      padding: 7px 0;
    }
  }
  .my-statistic{
    padding: 5px 5px 5px 21px;
  }

  .mCSB_inside > .mCSB_container{
    margin-right: 47px !important;
  }

  /**/
  .winners{
    padding: 7px 7px 7px 30px;
    margin-bottom: 15px;
    margin-top: 20px;
    td{
      font-size: 14px;
    }
  }

  /**/
  .official-rules{
    font-size: 13px;
    .scroll-box{
      height: 400px;
    }
    .mCSB_inside > .mCSB_container {
      margin-right: 0 !important;
      padding: 0 30px 0 20px;
    }
  }
} 

@media (max-width: 992px){
  .modal-dialog, .modal-dialog-centered{
    margin-top: 190px;
    min-height: auto;
  }
}

