@import "variables";

/* HEADER */
.main-header{
  transition-duration: 0.2s;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  padding: 30px 70px 0;
  display: flex;
  align-items: center;
}
.main-logo{
  position: absolute;
  right: 88px;
  top: -70px;
  width: 152px;
  z-index: 9000;
  img{
    width: 100%;
  }
}

/* NAV */
.nav-trigger{
  position: relative;
  width: 60px;
  height: 50px;
  border: none;
  background: none;
  cursor: pointer;
  margin: 0;
  display: block;
  margin-right: 5px;
  span{
    width: 30px;
    height: 5px;
    background: #fff;
    position: absolute;
    top: 13px;
    left: 50%;
    margin-left: -13px;
    transform: skewX(-15deg);
    transition-duration: 0.2s;
    &:nth-child(2){
      top: 21px;
      margin-left: -15px;
    }
    &:nth-child(3){
      top: 29px;
      margin-left: -17px;
    }
  }
  &.active{
    span{
      transform-origin: center center;
      transform: rotate(45deg);
      top: 22px;
      background: $color-r;
      width: 22px;
      margin-left: -10px;
      &:nth-child(2){
        opacity: 0;
        transform: rotate(0);
      }
      &:nth-child(3){
        transform: rotate(-45deg);
        top: 22px;
        width: 27px;
        margin-left: -13px;
      }
    }
  }
}

.header-menu{
  ul{
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    li{
      a{
        display: block;
        padding: 0 26px;
        font-family: $font-2;
        font-size: 22px;
        font-weight: 800;
        font-style: italic;
        text-transform: uppercase;
      }
    }
  }
}

.main-menu-wrap{
  z-index: 900;
  padding: 30px 15px 15px 70px;
  position: fixed;
  top: 0;
  left: 0;
  width: 495px;
  height: 100vh;
  transform: translateX(-115%);
  transition: transform 0.3s;
  &:before{
    content: '';
    position: absolute;
    left: -100px;
    top: -100px;
    right: -30px;
    bottom: -100px;
    background: #fff;
    opacity: 0.37;
    z-index: -3;
    transform: rotate(-4deg);
  }
  &:after{
    content: '';
    position: absolute;
    left: -100px;
    top: -100px;
    right: -5px;
    bottom: -100px;
    background: #fff;
    opacity: 0.74;
    z-index: -1;
    transform: rotate(-3deg);
  }
  &.active{
    transform: translateX(0);
  }
}
.main-menu-head{
  display: flex;
  align-items: center;
  margin-bottom: 100px;
  ul{
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    li{
      a{
        display: block;
        padding: 0 26px;
        font-family: $font-2;
        color: $color-1;
        font-size: 22px;
        font-weight: 800;
        font-style: italic;
        text-transform: uppercase;
        text-decoration: none;
      }
      &:hover{
        a{
          color: #218cd7;
        }
      }
    }
  }
}
.main-menu{
  ul{
    margin: 0;
    padding: 0;
    list-style: none;
    position: relative;
    li{
      display: block;
      position: relative;
      margin-bottom: 4px;
      a{
        display: block;
        font-size: 22px;
        font-family: $font-2;
        font-weight: 800;
        font-style: italic;
        text-transform: uppercase;
        text-decoration: none;
        color: $color-1;
        padding: 0 20px;
      }
      &:hover{
        a{
          color: #218cd7;
        }
      }
    }
  }
}

.bg-hide{
  position: fixed;
  left: 0;
  top: 0;
  width: 0;
  height: 0;
  transition: opacity .4s;
  opacity: 0;
  background: $color-1;
  z-index: -1;
  visibility: hidden;
}
.menu-active .bg-hide{
  width: 100%;
  height: 100%;
  visibility: visible;
  display: block;
  opacity: 0.7;
  z-index: 150;
}

@media (max-width: 1300px){
  .main-header{
    padding: 25px 34px 0;
  }
  .nav-trigger{
    transform: scale(0.7);
    margin-right: 0;
  }
  .header-menu ul li a{
    font-size: 15px;
    padding: 0 10px;
  }
  .main-logo{
    right: 22px;
    top: -42px;
    width: 106px;
  }

  .main-menu-wrap{
    padding: 25px 15px 15px 34px;
    width: 385px;
    &:before{
      right: -25px;
    }
    &:after{
      right: -3px;
    }
  }
  .main-menu-head{
    margin-bottom: 75px;
    ul{
      li{
        a{
          padding: 0 12px;
          font-size: 15px;
        }
      }
    }
  }
  .main-menu{
    ul{
      li{
        margin-bottom: 3px;
        a{
          font-size: 18px;
          padding: 0 18px;
        }
      }
    }
  }
}

@media (max-width: 991px){
  .main-header{
    padding: 13px 34px 0;
  }
  .header-menu ul li a{
    font-size: 14px;
    padding: 0 13px;
  }
  .main-logo{
    right: 35px;
    top: -40px;
    width: 97px;
  }

  .main-menu-wrap{
    padding: 14px 15px 15px 34px;
    width: 395px;
  }
  .main-menu-head{
    margin-bottom: 103px;
    ul{
      li{
        a{
          padding: 0 13px;
          font-size: 14px;
        }
      }
    }
  }
  .main-menu{
    ul{
      li{
        a{
          font-size: 21px;
          padding: 0 18px;
        }
      }
    }
  }
}

@media (max-width: 575px){
  .main-header{
    padding: 0px 12px 0;
  }
  .nav-trigger{
    margin-top: -4px;
  }
  .header-menu ul li a{
    font-size: 13px;
    padding: 0 15px;
  }
  .main-logo{
    right: 17px;
    top: -26px;
    width: 55px;
  }

  .main-menu-wrap{
    padding: 0px 15px 15px 12px;
    width: 395px;
  }
  .main-menu-head{
    margin-bottom: 65px;
    ul{
      li{
        a{
          padding: 0 15px;
          font-size: 13px;
        }
      }
    }
  }
  .main-menu{
    ul{
      li{
        margin-bottom: 12px;
        a{
          font-size: 15px;
          padding: 0 23px;
        }
      }
    }
  }
}

@media (max-width: 482px){
  .main-logo{
    z-index: 10;
  }
  .main-menu-wrap{
    width: 100%;
    &:after{
      left: 12px;
      right: 12px;
      transform: skewX(1.3deg);
    }
  }
}